*{margin:0;padding:0;box-sizing:border-box;font-family:"Raleway",sans-serif}h1{font-size:18px}

.App{display:flex;flex-direction:column;height:100vh}.App header{display:flex;flex-direction:row;justify-content:center;align-items:center;flex-basis:8%;background-color:#1b242f}.App section{justify-content:center;align-items:center;display:flex;flex-basis:78%;background-color:#ffffff}.App nav{flex-direction:column;display:flex;align-self:stretch;flex-basis:6%}.App .footer{display:flex;justify-content:space-between;align-items:center;background-color:#1b242f;border-top:#04c2c9 5px solid;flex-basis:8%}.App .footer__paragraph{margin-left:25px;color:#FFF;font-size:20px}.App .footer__highlight{color:#e31b6d}.App .footer__socials{display:flex;flex-basis:35%;justify-content:space-around;align-items:center}@media ((min-width: 760px)){.App .footer__socials{flex-basis:20%}}@media ((min-width: 1024px)){.App .footer__socials{flex-basis:15%}}.App .footer__social-link{display:block}.App .footer__social-link:hover{-webkit-animation:socialsFooter .6s linear forwards;animation:socialsFooter .6s linear forwards}.App .footer__social-icon-li{color:#0077b5}.App .footer__social-icon-gh{color:#fff}@-webkit-keyframes socialsFooter{0%{transform:scale(1, 1)}25%{transform:scale(1.2, 1.2)}50%{transform:scale(1.2, 1.2)}75%{transform:scale(1.2, 1.2)}100%{transform:scale(1, 1)}}@keyframes socialsFooter{0%{transform:scale(1, 1)}25%{transform:scale(1.2, 1.2)}50%{transform:scale(1.2, 1.2)}75%{transform:scale(1.2, 1.2)}100%{transform:scale(1, 1)}}

.navigation{align-self:stretch;display:flex;justify-content:center;background-color:#1b242f;border-bottom:5px solid #04c2c9}@media ((min-width: 760px)){.navigation{justify-content:flex-start}}@media ((min-width: 1024px)){.navigation{justify-content:space-between}}.navigation__socials{display:none}@media ((min-width: 1024px)){.navigation__socials{display:flex;width:15%;margin-right:10px;justify-content:space-around}}@media ((min-width: 1024px)){.navigation__social-link{display:block;margin:0 auto}.navigation__social-link:hover{-webkit-animation:socials .6s linear forwards;animation:socials .6s linear forwards}}@media ((min-width: 1024px)){.navigation__social-icon-li{color:#0077b5}}@media ((min-width: 1024px)){.navigation__social-icon-gh{color:#fff}}.navigation__list{display:flex;justify-content:center;flex-basis:100%;margin-bottom:5px;list-style:none}@media ((min-width: 760px)){.navigation__list{flex-basis:60%;margin-bottom:8px}}@media ((min-width: 1024px)){.navigation__list{flex-basis:40%;justify-content:flex-start;margin-bottom:20px}}.navigation__item{display:flex;justify-content:space-evenly;flex-basis:20%}.navigation__link{position:relative;display:block;padding:5px 6px;margin:5px;color:#FFF;transition:.4s;text-transform:uppercase;text-decoration:none;text-align:center;font-size:16px;font-family:"Raleway",sans-serif}.navigation__link:before{position:absolute;width:0%;content:'';height:2px;background-color:#04c2c9;bottom:1px;left:50%;transform:translateX(-50%);transition:.5s}.navigation__link:hover::before{height:4px;width:90%}.navigation__link--active::before{width:70%}@media ((min-width: 760px)){.navigation__link--active::before{height:3px}}@media ((min-width: 1024px)){.navigation__link--active::before{height:4px}}@media ((min-width: 760px)){.navigation__link{font-size:18px;padding:6px 8px}}@media ((min-width: 1024px)){.navigation__link{font-size:20px;padding:8px 10px}}.navigation__link--active{color:#FFF}@media ((min-width: 1024px)){.navigation__link--active{letter-spacing:2px}}.navigation__link:hover{color:#FFF;letter-spacing:2px}@-webkit-keyframes socials{0%{transform:scale(1, 1)}25%{transform:scale(1.2, 1.2)}50%{transform:scale(1.2, 1.2)}75%{transform:scale(1.2, 1.2)}100%{transform:scale(1, 1)}}@keyframes socials{0%{transform:scale(1, 1)}25%{transform:scale(1.2, 1.2)}50%{transform:scale(1.2, 1.2)}75%{transform:scale(1.2, 1.2)}100%{transform:scale(1, 1)}}

.contact{display:flex;flex-direction:column;width:100%;height:100%;justify-content:space-evenly;align-items:center}.contact__text-wraper{display:flex;flex-direction:column;justify-content:center;align-items:center}.contact__title{text-transform:uppercase;position:relative;padding:5px 0}@media ((min-width: 760px)){.contact__title{font-size:30px}}.contact__title:before{position:absolute;width:70%;content:'';height:4px;background-color:#04c2c9;bottom:1px;left:50%;transform:translateX(-50%);transition:.5s}.contact__title:hover::before{width:90%}.contact__paragraph{text-align:center;font-size:15px;line-height:30px}@media ((min-width: 760px)){.contact__paragraph{margin-top:25px;font-size:22px}}.contact__form{display:flex;flex-direction:column;width:90%;flex-basis:70%}@media ((min-width: 1024px)){.contact__form{width:50%}}.contact__input{background-color:#1b242f;border:3px #04c2c9 solid;margin-top:10px;flex-basis:10%;color:#fff;font-size:15px;letter-spacing:2px;padding:4px}.contact__message{flex-basis:60%}.contact__send-btn{width:25%;background-color:#1b242f;border:3px #04c2c9 solid;margin-top:10px;padding:15px 40px;color:#fff;text-transform:uppercase;font-weight:600;font-size:18px;transition:.4s}.contact__send-btn:hover{letter-spacing:4px}.contact__btn-wraper{display:flex;justify-content:center}

